.info img {
	margin-top: 20px;
	height: 220px;
}
.info {
	margin-top: 30px;
	text-align: center;
}
.info_word p {
	font-size: 15px;
	text-align: left;
	margin-bottom: 15px;
}

.info_pic {
	width: 60px;
	padding-right: 0;
	position: relative;
	top: 10px;
	left: 100px;
}
.info_word {
	width: 220px;
	position: relative;
	top: -195px;
	left: 175px;
}
.info_pic img {
	height: 50px;
	width: 50px;
	display: block;
}

@media (max-width: 1210px) {
	.info_pic {
		left: 80px;
	}
	.info_word {
		left: 150px;
		width: 210px;
	}
}

@media (max-width: 1120px) {
	.info_pic {
		left: 60px;
	}
	.info_pic img {
		margin-bottom: 30px;
	}
	.info_word {
		left: 130px;
		top: -235px;
		width: 200px;
	}
}
@media (max-width: 1020px) {
	.info_pic {
		left: 40px;
	}
	.info_word {
		left: 110px;
	}
}

@media (max-width: 930px) { /*mobile mode*/

}