@import url(https://fonts.googleapis.com/earlyaccess/cwtexfangsong.css);
@import url(https://fonts.googleapis.com/earlyaccess/cwtexyen.css);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "cwTeXFangSong", serif, helvetica, sans-serif;
}

.App-header * {
  font-family: "Pangolin", "ChalkboardSE-Light", "Comic Sans MS", "Comic Sans", cursive, sans-serif;
}

.App {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  background-image: url(/tblin_website_test/static/media/background.61f086ef.png);
  background-size: 100vw 100vh;
  background-position: center center;
  background-repeat: no-repeat;
  overflow: hidden;
}
p, ol {
  margin: 12px 0;
  padding: 0;
  font-size: 19px;
}

h3 {
  font-size: 28px;
}

li {
  margin-bottom: 8px;
}


.App-header {
  padding: 0;
  width: 100%;
  min-height: 10vh;
  display: inline-block;
  text-align: center;
  flex-direction: column;
  justify-content: center;
  font-size: 22px;
  color: rgb(120,0,0);
  position: fixed;
  top: 0;
  z-index: 50;
}

.dropdown_mobile, #dropdown_list_mobile {
  display: none;
}

#dropdown_list_mobile {
  background-color: rgb(120,0,0);
  opacity: 0.9;
  color: white;
  width: 100%;
  margin: 0;
}

.dropdown_mobile p {
  margin: 0 !important; 
}

#dropdown_list_mobile li{
  opacity: 0.9;
  font-size: 18px;
}


.title {
  margin: 10px 0 0 0;
}
.App-link {
  color: #61dafb;
}

#seperation_line {
    width: 80%;
    margin-left: auto;
    margin-right: auto;
    clear: both;
  }

.content {
  position: relative;
  top: 250px;
  z-index: 3;
  height: 95vh;
  overflow-y: auto;
  overflow-x: hidden; 
  font-size: 18px;
}

.navbar-item img {
  margin-left: auto;
  margin-right: auto;
  clear: both;
}
.column {
  vertical-align: text-top;
}
* .extended {
  padding-bottom: 300px;
}
.big {
  width: 48%;
  margin-right: 10%;
  text-align: left;
}

.small {
  width: 30%;
}
.left {
  clear: both;
  float: left;
  margin-left: 10%;
}
.right {
  margin-left: 0;
  float: right;
}

.photo {
  max-width: 400px;
  max-height: 400px;
  margin-left: -100px;
  margin-top: 20px;
}

.footer {
  height: 40px;
  width: 100%;
  position: fixed;
  bottom: 0;
  text-align: center;
  color: white;
  background-color: rgb(120,0,0);
}
.static_profile {
  position: fixed;
  top: 240px;
}
.static_publications {
  position: fixed;
  top: 240px;
  left: 70%;
}
.static_team {
  position: fixed;
  top: 300px;
  left: 5%;
}
.static_others {
  position: fixed;
  top: 300px;
  left: 70%;
}
.navbar-item {
  width: 19%;
  color: black;
}
.navbar-item:hover {
  cursor: pointer;
}

.navbar-click span{
  padding: 0 10px 3px 10px;
  border-radius: 100px;
  background-color: rgb(120,0,0);
  color: white;
}
.footer {
  z-index: 100;
}
.footer a {
  position: relative;
  bottom: -4px;
  left: 17%;
  margin-right: 8px;
  
}
.footer a img {
  width: 35px;
  height: 35px;
  display: inline-block;
}
.footer p {
  margin: 0;
  font-size: 18px;
  position: fixed;
  bottom: 8px;
  left: 35%;
  display: inline-block;
}

.switch {
  position: fixed;
  display: inline-block;
  bottom: 15px;
  right: 200px;
}

.switch a {
    list-style-type: none;
    text-decoration: none;
    color: white;
    padding: 4px 10px;
    border-radius: 5px 5px 5px 5px;
    min-width: 100px;
    width: 150px;
    margin: 0;
}


#deactive-tag {
  background-color: rgb(180,20,20);
}

#active-tag {
    background-color: rgb(230,20,20);
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@media (max-width: 1350px) {
  .footer p {
    left: 29%;
  }
  .footer a {
    left: 190px;
  }
  .switch {
    right: 330px;
  }
}

@media (max-width: 1120px) {
  .footer a {
    left: 230px;
  }
  .switch {
    right: 300px;
  }
  #seperation_line {
    width: 90%;
    margin-left: auto;
    margin-right: auto;
    clear: both;
  }
}
@media (max-width: 1020px) {
  .footer p {
    left: 21%;
  }
  .footer a {
    left: 180px;
  }
  .switch {
    right: 250px;
  }
}

/*mobile frame*/
@media (max-width: 900px) {
  .title {
    position: fixed;
    font-size: 22px;
    margin: 0;
    width: 100%;
    background-color: rgb(120,0,0);
    color: white;
    padding-top: 8px;
    padding-bottom: 8px;
    text-align: center;
    margin-top: -10px;
  }
  .dropdown_all {
    display: none;
  }
  .dropdown_mobile {
    display: block;
    position: relative;
    background-color: gray;
    color: black;
    top: 35px;
    text-align: left;
  }

  .content {
    top: 70px;
  }
  .small {
    display: none;
  }
  .big {
    width: 85%;
  }

  .footer p{
    font-size: 16px;
  }
  .switch {
    display: none;
  }
}

@media (max-width: 715px) {
  .footer a {
    display: none;
  }
}


.navbar-item {
	display: inline-block;
	font-size: 24px;

}
.navbar-item img {
	height: 100px;
	width: auto;
	min-width: 50px;
}
.dropdown_all {
	width: 90%;
	height: 170px;
	margin-left: auto;
	margin-right: auto;
	clear: both;
}

@media (max-width: 1120px) {
	.dropdown_all {
		width: 100%;
	}
}
.dropdown_mobile img{
	width: 20px;
	margin-left: 8px;
	margin-right: 8px;
}
.profileIntro {
	margin-left: 15px;
	margin-bottom: 30px;
}

.profileOthers p, .profileOthers table, .profileOthers ol{
	margin-left: 30px;
}
.profileOthers h3 {
	margin-top: 20px;
}
.info img {
	margin-top: 20px;
	height: 220px;
}
.info {
	margin-top: 30px;
	text-align: center;
}
.info_word p {
	font-size: 15px;
	text-align: left;
	margin-bottom: 15px;
}

.info_pic {
	width: 60px;
	padding-right: 0;
	position: relative;
	top: 10px;
	left: 100px;
}
.info_word {
	width: 220px;
	position: relative;
	top: -195px;
	left: 175px;
}
.info_pic img {
	height: 50px;
	width: 50px;
	display: block;
}

@media (max-width: 1210px) {
	.info_pic {
		left: 80px;
	}
	.info_word {
		left: 150px;
		width: 210px;
	}
}

@media (max-width: 1120px) {
	.info_pic {
		left: 60px;
	}
	.info_pic img {
		margin-bottom: 30px;
	}
	.info_word {
		left: 130px;
		top: -235px;
		width: 200px;
	}
}
@media (max-width: 1020px) {
	.info_pic {
		left: 40px;
	}
	.info_word {
		left: 110px;
	}
}

@media (max-width: 930px) { /*mobile mode*/

}
.static_publications img {
	width: 280px;
	margin-top: 30px;
	margin-left: -60px;
}


.publications_content li {
	margin-left: 30px;
}
.google_scholar img {
	width: 300px;
	position: relative;
	top: -30px;
	left: -8px;
}
.block {
	display: block;
}
.pdf_icon {
	width: 30px;
	margin-left: 10px;
}

@media (max-width: 1120px) {
	.static_publications img {
		margin-left: -40px;
		margin-top: 30px;
		width: 90%;
	}
	.google_scholar img {
		left: 0px;
		top: -30px;
	}
}
.projects_content li {
	margin-left: 30px;
}
.photo {
	margin-top: 0;
}

@media (max-width: 1120px) {
	.photo {
		width: 100%;
		margin-left: -70px;
	}
}
.team_contents li {
	margin-left: 30px;
}
.services_content li {
	margin-left: 30px;
}
